import Vue from "vue";
import Router from "vue-router";
import Vuetify from "vuetify";
import "vuetify/dist/vuetify.min.css";

import RegistroIndicador from "../components/RegistroIndicador.vue";
import IndicadorPersona from "../components/IndicadorPersona.vue";
import AsignacionIndicadorGrupo from "../components/AsignacionIndicadorGrupo.vue";
import Login from "../components/Login.vue";
import Dashboard from "../components/Dashboard.vue";

Vue.use(Router);
Vue.use(Vuetify);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/",
      name: "login",
      component: Login
    },
    {
      path: "/logout",
      name: "logout",
      component: Login
    },
    {
      path: "/dashboard",
      name: "dashboard",
      component: Dashboard,
      children: [
        {
          path: "/dashboard/registroIndicador",
          name: "registroIndicador",
          component: RegistroIndicador
        },
        {
          path: "/dashboard/indicadorPersona",
          name: "indicadorPersona",
          component: IndicadorPersona
        },
        {
          path: "/dashboard/asignacionIndicadorGrupo",
          name: "asignacionIndicadorGrupo",
          component: AsignacionIndicadorGrupo
        }
      ]
    }
  ]
});
